exports.getElements = function (node, tagName) {
  if (!node || !node.getElementsByTagName(tagName)) {
    return [];
  }

  var elements = node.getElementsByTagName(tagName);
  return Array.prototype.slice.call(elements);
};

exports.getChildElements = function (node, tagName, namespace) {
  if (!node) {
    return [];
  }

  var elements = namespace ? node.getElementsByTagNameNS(namespace, tagName) : node.getElementsByTagName(tagName);

  if (!elements) {
    return [];
  }

  return Array.prototype.filter.call(elements, function (element) {
    return element.parentNode.nodeName === node.nodeName;
  });
};

exports.getElementTextContentArray = function (node, tagName, namespace) {
  var nodes = this.getChildElements(node, tagName, namespace);

  if (!nodes || nodes.length === 0) {
    return [];
  }

  return nodes.map(function (node) {
    return node.textContent;
  });
};

exports.getElementTextContent = function (node, tagName, namespace) {
  var array = this.getElementTextContentArray(node, tagName, namespace);
  return array.length === 0 ? undefined : array[0];
};